import Vue from "vue";
import Vuex from "vuex";
import stroageUtill from './assets/stroageUtill.js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    //输入框里的内容 item
    item: {
      content: "",
      iscomplete: false
    },
    //存放着item的数组
    // listitem: []
    listitem: stroageUtill.readTodos()
  },
  mutations: {
    //更新item中的content数据
    updateitem(state, value) {
      state.item.content = value;
      stroageUtill.saveTodos(state.listitem);
    },
    //添加数据到listitem中
    pushitem(state, value) {
      state.listitem.push(value);
      stroageUtill.saveTodos(state.listitem);
    },
    //删除listitem中某个id的item
    deleteritem(state, id) {
      state.listitem.forEach((el, index) => {
        if (el.id === id) {
          state.listitem.splice(index, 1);
        }
        // return index;
      });
      stroageUtill.saveTodos(state.listitem);
    }
  },
  actions: {},
  getters: {
    //过滤listitem中已完成的item
    completelist: state => {
      return state.listitem.filter(el => {
        return el.iscomplete === false;
      });
    },
    //过滤listitem中未完成的item
    unfinishedlist: state => {
      return state.listitem.filter(el => {
        return el.iscomplete === true;
      });
    }
  }
});
