<template>
  <!-- <div> -->
  <nav class="navbar navbar-default">
    <div class="container-fluid form-center">
      <form class="navbar-form" role="search">
        <div class="form-group">
          <input
            type="text"
            class="form-control input-text"
            placeholder="添加任务"
            v-model="itemcontent"
            @keyup.enter="pushitem"
          >
        </div>
        <button type="submit" class="btn butt btn-default">添加</button>
      </form>
    </div>
  </nav>
  <!-- </div> -->
</template>

<script>
let Id=0;
export default {
  name: "vheader",
  computed: {
    itemcontent: {
      get() {
        return this.$store.state.item.content;
      },
      set(value) {
        this.$store.commit("updateitem", value);
      }
    },
    itemiscomplete() {
      return this.$store.state.item.iscomplete;
    }
  },

  methods: {
    pushitem() {
      // let inputcontent=this.itemcontent.trim()
      if (this.itemcontent === "") {
        alert('内容不能为空，请输入内容');
      } else {
        let item = {
          id:Id++,
          content: this.itemcontent,
          iscomplete: this.itemiscomplete
        };
        this.$store.commit("pushitem", item); //添加数据到listitem中
        this.itemcontent = "";
      }
    }
  }
  // mounted(){
  //   console.log(this.itemcontent);
  //   console.log(this.itemiscomplete);
  // }
};
</script>

<style scoped>
.form-center {
  display: flex;
  justify-content: center;
}
.input-text {
  /* width: 60rem; */
  width: 700px;
}
.butt {
  margin-left: 1em;
}
</style>
